form{
    background: #F9F9F9;
    padding: 25px;
    margin: 50px auto;
    box-shadow: 0 0 20px 0 rgb(0 0 0 / 20%), 0 5px 5px 0 rgb(0 0 0 / 24%);
    max-width: 400px;
}
fieldset{
    border: medium none !important;
    margin: 0 0 10px;
    min-width: 100%;
    padding: 0;
    width: 100%;
}
table{
    margin: 0 auto;
    border: 1px solid;
    width:90% !important;
}
table th, table td{
    border:1px solid;
    text-align: center;
}
.form-input input{
    width: 100%;
    border: 1px solid #ccc;
    background: #FFF;
    margin: 0 0 5px;
    padding: 10px;
}

table img{
    width: 128px;
    height: 128px;
}

@media only screen and (max-width:425px) {
    .container{
        padding: 0;
    }
    table button{
        width: 60px;
        margin: 0 4px;
        padding: 4px 4px !important;
    }
    table td, table th{
        font-size: 15px;
        width: 107px;
        word-break: break-word;
    }
    table img{
        width: 50px;
        height: 50px;
    }
    body{
        width: fit-content;
    }
    form{
        margin-left: 25px;
        margin-right: 25px;
    }
    table tr > th:first-child, table tr > td:first-child {
        width: 40px;
    }
}

